<template>
  <el-dialog
    class="m-orderManage-auditDialog"
    :visible="visible"
    title="客服审核"
    append-to-body
    width="900px"
    @close="closeEvent">
    <el-row v-if="target.sign.status === -1" style="padding: 10px 20px;border:2px red dotted;margin-bottom:20px;">
      驳回理由：{{ target.bill_info.reject }}
    </el-row>
    <el-row :gutter="20" style="padding: 10px 20px;border:1px #ddd solid;margin-bottom:20px;">
      <el-form :model="target.bill_info" ref="itemForm" label-position="top" label-width="150px" :disabled="!editContactNumber">
        <el-row>
          <el-col :span="8">
            <el-form-item label="合同号" prop="customer_name">
              <el-col :span="24">
                <el-input v-model="target.bill_info.contract_number"></el-input>
              </el-col>
            </el-form-item>
          </el-col>
          <el-col :span="2" v-if="!editContactNumber" style="margin-top:50px;margin-left:10px;">
            <el-button type="primary" @click="onEditContractNumber">修改</el-button>
          </el-col>
          <el-col :span="2" v-if="editContactNumber" style="margin-top:50px;margin-left:10px;">
            <el-button type="primary" @click="onEditContractNumber">保存</el-button>
          </el-col>
        </el-row>
      </el-form>
      <el-row>
        <el-form>
          <el-col :span="4">
            <el-form-item label="公司">
              <el-select v-model="contract.company" placeholder="请选择">
                <el-option
                  v-for="item in companies"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col v-if="contract.type === '个人'" :span="6">
            <el-form-item label="产品">
              <el-select v-model="contract.product" placeholder="请选择">
                <el-option
                  v-for="item in products"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="4" style="padding: 40px 0;margin-left:10px;">
            <el-button type="primary" @click="onDownloadContract">下载合同模版</el-button>
          </el-col>
        </el-form>
      </el-row>
    </el-row>
    <div class="detail-title" style="border-bottom:none;">客户：<el-button style="font-size:16px;" @click.native.prevent="onCheckEvent" type="text">{{ target.customer.name }}</el-button></div>
    <el-form :model="item" ref="itemForm" label-position="right" label-width="150px">
      <el-row>
        <el-col :span="12">
          <el-form-item label="不可撤销承诺书" required>
            <el-col :span="24">
              <el-upload
                action="/v1/inspect/upload/file"
                list-type="picture"
                :on-change="handleChange_chennuoshu"
                :on-preview="handlePreview"
                :before-remove="beforeRemove"
                :file-list="item.chennuoshu">
                <el-button size="small" type="primary">点击上传</el-button>
              </el-upload>
            </el-col>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="单签公司合同" required>
            <el-col :span="24">
              <el-upload
                action="/v1/inspect/upload/file"
                list-type="picture"
                :on-change="handleChange_danqian"
                :on-preview="handlePreview"
                :before-remove="beforeRemove"
                :file-list="item.danqian">
                <el-button size="small" type="primary">点击上传</el-button>
              </el-upload>
            </el-col>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="抵押合同" required>
            <el-col :span="24">
              <el-upload
                action="/v1/inspect/upload/file"
                list-type="picture"
                :on-change="handleChange_mortgage_contract"
                :on-preview="handlePreview"
                :before-remove="beforeRemove"
                :file-list="item.mortgage_contract">
                <el-button size="small" type="primary">点击上传</el-button>
              </el-upload>
            </el-col>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="反担保补充协议" required>
            <el-col :span="24">
              <el-upload
                action="/v1/inspect/upload/file"
                list-type="picture"
                :on-change="handleChange_fandanbaobuchong"
                :on-preview="handlePreview"
                :before-remove="beforeRemove"
                :file-list="item.fandanbaobuchong">
                <el-button size="small" type="primary">点击上传</el-button>
              </el-upload>
            </el-col>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="反担保协议" required>
            <el-col :span="24">
              <el-upload
                action="/v1/inspect/upload/file"
                list-type="picture"
                :on-change="handleChange_fandanbao"
                :on-preview="handlePreview"
                :before-remove="beforeRemove"
                :file-list="item.fandanbao">
                <el-button size="small" type="primary">点击上传</el-button>
              </el-upload>
            </el-col>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="反欺诈风险提示单页" required>
            <el-col :span="24">
              <el-upload
                action="/v1/inspect/upload/file"
                list-type="picture"
                :on-change="handleChange_fanqizha"
                :on-preview="handlePreview"
                :before-remove="beforeRemove"
                :file-list="item.fanqizha">
                <el-button size="small" type="primary">点击上传</el-button>
              </el-upload>
            </el-col>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="车辆交接单" required>
            <el-col :span="24">
              <el-upload
                action="/v1/inspect/upload/file"
                list-type="picture"
                :on-change="handleChange_cheliangjiaojie"
                :on-preview="handlePreview"
                :before-remove="beforeRemove"
                :file-list="item.cheliangjiaojie">
                <el-button size="small" type="primary">点击上传</el-button>
              </el-upload>
            </el-col>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="付款时间表" required>
            <el-col :span="24">
              <el-upload
                action="/v1/inspect/upload/file"
                list-type="picture"
                :on-change="handleChange_fukuanshijian"
                :on-preview="handlePreview"
                :before-remove="beforeRemove"
                :file-list="item.fukuanshijian">
                <el-button size="small" type="primary">点击上传</el-button>
              </el-upload>
            </el-col>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="汽车租赁合同" required>
            <el-col :span="24">
              <el-upload
                action="/v1/inspect/upload/file"
                list-type="picture"
                :on-change="handleChange_qichezuling"
                :on-preview="handlePreview"
                :before-remove="beforeRemove"
                :file-list="item.qichezuling">
                <el-button size="small" type="primary">点击上传</el-button>
              </el-upload>
            </el-col>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="授权委托书" required>
            <el-col :span="24">
              <el-upload
                action="/v1/inspect/upload/file"
                list-type="picture"
                :on-change="handleChange_shouquan"
                :on-preview="handlePreview"
                :before-remove="beforeRemove"
                :file-list="item.shouquan">
                <el-button size="small" type="primary">点击上传</el-button>
              </el-upload>
            </el-col>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="租赁申请核准通知函" required>
            <el-col :span="24">
              <el-upload
                action="/v1/inspect/upload/file"
                list-type="picture"
                :on-change="handleChange_zulinghezhun"
                :on-preview="handlePreview"
                :before-remove="beforeRemove"
                :file-list="item.zulinghezhun">
                <el-button size="small" type="primary">点击上传</el-button>
              </el-upload>
            </el-col>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="其他资料">
            <el-col :span="24">
              <el-upload
                action="/v1/inspect/upload/file"
                list-type="picture"
                :on-change="handleOtherChange"
                :on-preview="handlePreview"
                :before-remove="beforeRemove"
                :file-list="item.other">
                <el-button size="small" type="primary">点击上传</el-button>
              </el-upload>
            </el-col>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="审核意见">
            <el-input :style="{width: '300px'}" v-model="item.opinion" type="textarea" placeholder="请输入描述" :rows="4" />
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button type="primary" @click="onPassEvent">通 过</el-button>
      <!-- <el-button type="warning" @click="showEditDialog = false">驳 回</el-button> -->
      <el-button @click="visible = false">取 消</el-button>
    </div>
  </el-dialog>
</template>
<script>
import {ObjCopy} from '@/common/utils';
import { rest } from '@/common/request';
export default {
  props:['target'],
  data(){
    return{
      visible: true,
      editContactNumber: false,
      item: {
        score_card:{}
      },
      products:[
        {name: '按揭车不押车', id: '按揭车不押车'},
        {name: '按揭车全款车押车', id: '按揭车全款车押车'},
        {name: '河源牌押证', id: '河源牌押证'},
        {name: '惠州牌押证', id: '惠州牌押证'},
        {name: '信用贷', id: '信用贷'}
      ],
      types:[
        {name: '公司', id: '公司'},
        {name: '个人', id: '个人'}
      ],
      companies:[
        {name: '龙华', id: '龙华'},
        {name: '宝安', id: '宝安'},
        {name: '东莞', id: '东莞'},
        {name: '惠州', id: '惠州'}
      ],
      contract:{
        product: null,
        type: '公司',
        company: null
      }
    }
  },
  computed:{
    info(){
      return this.item.wind_control;
    }
  },
  methods:{
    onEditContractNumber(){
      let uid = sessionStorage.getItem('ts_user_id');
      if(this.editContactNumber){
        return rest({
        url: '/v1/inspect/'+ uid +'/bill/contract_number/update',
        headers:{
          'X-Inspect-Token': sessionStorage.getItem('ts_userToken')
        },
        method: 'POST',
        data: {
          bill_id: this.target.bill_info.bill_id,
          contract_number: this.target.bill_info.contract_number
        }
        }).then(res => {
          this.editContactNumber = !this.editContactNumber;
          this.$message({
            message: '编辑合同号成功！',
            type: 'success'
          });
        }).catch(res => {
          this.$message.error(res.response.data.msg);
        })
      }else{
        this.editContactNumber = !this.editContactNumber;
      }
    },
    handlePreview(file){
      window.open(file.response);
    },
    beforeRemove(file, fileList) {
      return this.$confirm(`确定移除 ${ file.name }？`);
    },
    handleChange_chennuoshu(file, fileList){
      this.item.chennuoshu = fileList;
    },
    handleChange_danqian(file, fileList){
      this.item.danqian = fileList;
    },
    handleChange_mortgage_contract(file, fileList){
      this.item.mortgage_contract = fileList;
    },
    handleChange_fandanbaobuchong(file, fileList){
      this.item.fandanbaobuchong = fileList;
    },
    handleChange_fandanbao(file, fileList){
      this.item.fandanbao = fileList;
    },
    handleChange_fanqizha(file, fileList){
      this.item.fanqizha = fileList;
    },
    handleChange_cheliangjiaojie(file, fileList){
      this.item.cheliangjiaojie = fileList;
    },
    handleChange_fukuanshijian(file, fileList){
      this.item.fukuanshijian = fileList;
    },
    handleChange_qichezuling(file, fileList){
      this.item.qichezuling = fileList;
    },
    handleChange_shouquan(file, fileList){
      this.item.shouquan = fileList;
    },
    handleChange_zulinghezhun(file, fileList){
      this.item.zulinghezhun = fileList;
    },
    handleOtherChange(file, fileList){
      this.item.other = fileList;
    },
    onCheckEvent(){
      this.$emit('detail');
    },
    closeEvent(){
      const that = this;
      this.visible = false;
      setTimeout(()=>{that.$emit('close');},500);
    },
    onDownloadContract(){
      let uid = sessionStorage.getItem('ts_user_id');
      
      if(this.contract.company === null){
        this.$message('请选择公司！');
        return false;
      }
      if(this.contract.type === null){
        this.$message('请选择类型！');
        return false;
      }
      if(this.contract.type === '个人' && this.contract.product === null){
        this.$message('请选择产品！');
        return false;
      }
      window.open(`/v1/inspect/${ uid }/contract?bill_id=${this.item.bill_id}&company=${this.contract.company}&type=${this.contract.type}${this.contract.type === '个人'?'&product='+this.contract.product:''}`);
    },
    onPassEvent(){
      let data = ObjCopy(this.item);
      if(data.chennuoshu.length === 0){
        this.$message('请上传不可撤销承诺书！');
        return false;
      }else{
        delete data.chennuoshu;
        data.chennuoshu = this.item.chennuoshu.map(item => item.response?item.response:item.url);
      }
      if(data.danqian.length === 0){
        this.$message('请上传单签公司合同！');
        return false;
      }else{
        delete data.danqian;
        data.danqian = this.item.danqian.map(item => item.response?item.response:item.url);
      }
      if(data.mortgage_contract.length === 0){
        this.$message('请上传抵押合同！');
        return false;
      }else{
        delete data.mortgage_contract;
        data.mortgage_contract = this.item.mortgage_contract.map(item => item.response?item.response:item.url);
      }
      if(data.fandanbaobuchong.length === 0){
        this.$message('请上传反担保补充协议！');
        return false;
      }else{
        delete data.fandanbaobuchong;
        data.fandanbaobuchong = this.item.fandanbaobuchong.map(item => item.response?item.response:item.url);
      }
      if(data.fandanbao.length === 0){
        this.$message('请上传反担保协议！');
        return false;
      }else{
        delete data.fandanbao;
        data.fandanbao = this.item.fandanbao.map(item => item.response?item.response:item.url);
      }
      if(data.fanqizha.length === 0){
        this.$message('请上传反欺诈风险提示单页！');
        return false;
      }else{
        delete data.fanqizha;
        data.fanqizha = this.item.fanqizha.map(item => item.response?item.response:item.url);
      }
      if(data.cheliangjiaojie.length === 0){
        this.$message('请上传车辆交接单！');
        return false;
      }else{
        delete data.cheliangjiaojie;
        data.cheliangjiaojie = this.item.cheliangjiaojie.map(item => item.response?item.response:item.url);
      }
      if(data.fukuanshijian.length === 0){
        this.$message('请上传付款时间表！');
        return false;
      }else{
        delete data.fukuanshijian;
        data.fukuanshijian = this.item.fukuanshijian.map(item => item.response?item.response:item.url);
      }
      if(data.qichezuling.length === 0){
        this.$message('请上传汽车租赁合同！');
        return false;
      }else{
        delete data.qichezuling;
        data.qichezuling = this.item.qichezuling.map(item => item.response?item.response:item.url);
      }
      if(data.shouquan.length === 0){
        this.$message('请上传授权委托书！');
        return false;
      }else{
        delete data.shouquan;
        data.shouquan = this.item.shouquan.map(item => item.response?item.response:item.url);
      }
      if(data.zulinghezhun.length === 0){
        this.$message('请上传租赁申请核准通知函！');
        return false;
      }else{
        delete data.zulinghezhun;
        data.zulinghezhun = this.item.zulinghezhun.map(item => item.response?item.response:item.url);
      }
      if(data.other.length === 0){
        
      }else{
        delete data.other;
        data.other = this.item.other.map(item => item.response?item.response:item.url);
      }
      this.$emit('pass', data);
    }
  },
  mounted(){
    if(!this.target.sign.sign_id){
      this.item = {   
        "bill_id": this.target.bill_info.bill_id,                //订单id 
        "bill_status": 4,           //审核之后的订单状态 (驳回到之前的任意一个审核状态，或者下个状态)
        "status": 1,                 //1通过 -1驳回
        "opinion": "无",         //审核意见
        // "sign_id": 1,      //更新的时候才需要带上

        "flag": 0,          //0表示公司 1表示个人
        "chennuoshu": [], //不可撤销承诺书
        "danqian": [],    //单签公司合同
        "mortgage_contract": [], //抵押合同
        "fandanbaobuchong": [],  //反担保补充协议
        "fandanbao": [],         //反担保协议
        "fanqizha": [],          //反欺诈风险提示单页
        "cheliangjiaojie": [],   //车辆交接单
        "fukuanshijian": [],     //付款时间表
        "qichezuling": [],       //汽车租赁合同
        "shouquan": [],          //授权委托书
        "zulinghezhun": [],      //租赁申请核准通知函
        "other":[]                   //其他资料
      }
    }else{
      this.item = this.target.sign;
      this.item.chennuoshu = this.item.chennuoshu.map(item => {
        return {name: '查看', url: item};
      });
      this.item.danqian = this.item.danqian.map(item => {
        return {name: '查看', url: item};
      });
      this.item.mortgage_contract = this.item.mortgage_contract.map(item => {
        return {name: '查看', url: item};
      });
      this.item.fandanbaobuchong = this.item.fandanbaobuchong.map(item => {
        return {name: '查看', url: item};
      });
      this.item.fandanbao = this.item.fandanbao.map(item => {
        return {name: '查看', url: item};
      });
      this.item.fanqizha = this.item.fanqizha.map(item => {
        return {name: '查看', url: item};
      });
      this.item.cheliangjiaojie = this.item.cheliangjiaojie.map(item => {
        return {name: '查看', url: item};
      });
      this.item.fukuanshijian = this.item.fukuanshijian.map(item => {
        return {name: '查看', url: item};
      });
      this.item.qichezuling = this.item.qichezuling.map(item => {
        return {name: '查看', url: item};
      });
      this.item.shouquan = this.item.shouquan.map(item => {
        return {name: '查看', url: item};
      });
      this.item.zulinghezhun = this.item.zulinghezhun.map(item => {
        return {name: '查看', url: item};
      });
      this.item.other = this.item.other.map(item => {
        return {name: '查看', url: item};
      });
      this.item.bill_id = this.target.bill_info.bill_id;
      this.item.bill_status = 4;
      this.item.flag = 0;
    }
  }
}
</script>
<style scoped>
  .m-orderManage-auditDialog .detail-title {
    line-height: 24px;
    font-size: 16px;
    color: #2d2f33;
    border-bottom: 1px #ddd solid;
    margin-bottom: 10px;
    padding: 0 30px;
  }
  .m-orderManage-auditDialog .el-form {
    padding: 20px;
  }
  .m-orderManage-auditDialog .score_card_panel{
    border: 2px #ddd dotted;
    margin-bottom: 20px;
  }
  .m-orderManage-auditDialog .score_card_panel .title{
    padding: 20px;
    font-size: 16px;
    font-weight: bold;
  }
</style>




