<template>
  <el-dialog
    class="m-orderManage-auditDialog"
    :visible="visible"
    title="客服审核"
    append-to-body
    width="900px"
    @close="closeEvent">
    <el-row v-if="target.sign.status === -1" style="padding: 10px 20px;border:2px red dotted;margin-bottom:20px;">
      驳回理由：{{ target.bill_info.reject }}
    </el-row>
    <el-row :gutter="20" style="padding: 10px 20px;border:1px #ddd solid;margin-bottom:20px;">
      <el-form :model="target.bill_info" ref="itemForm" label-position="top" label-width="150px" :disabled="!editContactNumber">
        <el-row>
          <el-col :span="8">
            <el-form-item label="合同号" prop="customer_name">
              <el-col :span="24">
                <el-input v-model="target.bill_info.contract_number"></el-input>
              </el-col>
            </el-form-item>
          </el-col>
          <el-col :span="2" v-if="!editContactNumber" style="margin-top:50px;margin-left:10px;">
            <el-button type="primary" @click="onEditContractNumber">修改</el-button>
          </el-col>
          <el-col :span="2" v-if="editContactNumber" style="margin-top:50px;margin-left:10px;">
            <el-button type="primary" @click="onEditContractNumber">保存</el-button>
          </el-col>
        </el-row>
      </el-form>
      <el-row>
        <el-form>
          <el-col :span="4">
            <el-form-item label="公司">
              <el-select v-model="contract.company" placeholder="请选择">
                <el-option
                  v-for="item in companies"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col v-if="contract.type === '个人'" :span="6">
            <el-form-item label="产品">
              <el-select v-model="contract.product" placeholder="请选择">
                <el-option
                  v-for="item in products"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="4" style="padding: 40px 0;margin-left:10px;">
            <el-button type="primary" @click="onDownloadContract">下载合同模版</el-button>
          </el-col>
        </el-form>
      </el-row>
    </el-row>
    <div class="detail-title" style="border-bottom:none;">客户：<el-button style="font-size:16px;" @click.native.prevent="onCheckEvent" type="text">{{ target.customer.name }}</el-button></div>
    <el-form :model="item" ref="itemForm" label-position="right" label-width="150px">
      <el-row>
        <el-col :span="12">
          <el-form-item label="借款合同" required>
            <el-col :span="24">
              <el-upload
                action="/v1/inspect/upload/file"
                list-type="picture"
                :on-change="handleLoanContractChange"
                :on-preview="handlePreview"
                :before-remove="beforeRemove"
                :file-list="item.loan_contract">
                <el-button size="small" type="primary">点击上传</el-button>
              </el-upload>
            </el-col>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="抵押合同" required>
            <el-col :span="24">
              <el-upload
                action="/v1/inspect/upload/file"
                list-type="picture"
                :on-change="handleMortgageContractChange"
                :on-preview="handlePreview"
                :before-remove="beforeRemove"
                :file-list="item.mortgage_contract">
                <el-button size="small" type="primary">点击上传</el-button>
              </el-upload>
            </el-col>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="车辆转让协议" required>
            <el-col :span="24">
              <el-upload
                action="/v1/inspect/upload/file"
                list-type="picture"
                :on-change="handleCarTransferChange"
                :on-preview="handlePreview"
                :before-remove="beforeRemove"
                :file-list="item.car_transfer">
                <el-button size="small" type="primary">点击上传</el-button>
              </el-upload>
            </el-col>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="借款居间协议" required>
            <el-col :span="24">
              <el-upload
                action="/v1/inspect/upload/file"
                list-type="picture"
                :on-change="handleLoanIntermediaryChange"
                :on-preview="handlePreview"
                :before-remove="beforeRemove"
                :file-list="item.loan_intermediary">
                <el-button size="small" type="primary">点击上传</el-button>
              </el-upload>
            </el-col>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="还款计划表" required>
            <el-col :span="24">
              <el-upload
                action="/v1/inspect/upload/file"
                list-type="picture"
                :on-change="handleRepaymentPlanChange"
                :on-preview="handlePreview"
                :before-remove="beforeRemove"
                :file-list="item.repayment_plan">
                <el-button size="small" type="primary">点击上传</el-button>
              </el-upload>
            </el-col>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="借款收据" required>
            <el-col :span="24">
              <el-upload
                action="/v1/inspect/upload/file"
                list-type="picture"
                :on-change="handleLoanReceiptChange"
                :on-preview="handlePreview"
                :before-remove="beforeRemove"
                :file-list="item.loan_receipt">
                <el-button size="small" type="primary">点击上传</el-button>
              </el-upload>
            </el-col>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="机动车委托书" required>
            <el-col :span="24">
              <el-upload
                action="/v1/inspect/upload/file"
                list-type="picture"
                :on-change="handleAttorneyPowerChange"
                :on-preview="handlePreview"
                :before-remove="beforeRemove"
                :file-list="item.attorney_power">
                <el-button size="small" type="primary">点击上传</el-button>
              </el-upload>
            </el-col>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="融资服务协议" required>
            <el-col :span="24">
              <el-upload
                action="/v1/inspect/upload/file"
                list-type="picture"
                :on-change="handleFinanceServicesAgreementChange"
                :on-preview="handlePreview"
                :before-remove="beforeRemove"
                :file-list="item.finance_services_agreement">
                <el-button size="small" type="primary">点击上传</el-button>
              </el-upload>
            </el-col>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="放款审批表" required>
            <el-col :span="24">
              <el-upload
                action="/v1/inspect/upload/file"
                list-type="picture"
                :on-change="handleLoanApprovalFormChange"
                :on-preview="handlePreview"
                :before-remove="beforeRemove"
                :file-list="item.loan_approval_form">
                <el-button size="small" type="primary">点击上传</el-button>
              </el-upload>
            </el-col>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="其他资料">
            <el-col :span="24">
              <el-upload
                action="/v1/inspect/upload/file"
                list-type="picture"
                :on-change="handleOtherChange"
                :on-preview="handlePreview"
                :before-remove="beforeRemove"
                :file-list="item.other">
                <el-button size="small" type="primary">点击上传</el-button>
              </el-upload>
            </el-col>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="审核意见">
            <el-input :style="{width: '300px'}" v-model="item.opinion" type="textarea" placeholder="请输入描述" :rows="4" />
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button type="primary" @click="onPassEvent">通 过</el-button>
      <!-- <el-button type="warning" @click="showEditDialog = false">驳 回</el-button> -->
      <el-button @click="visible = false">取 消</el-button>
    </div>
  </el-dialog>
</template>
<script>
import {ObjCopy} from '@/common/utils';
import { rest } from '@/common/request';
export default {
  props:['target'],
  data(){
    return{
      visible: true,
      editContactNumber: false,
      item: {
        score_card:{}
      },
      products:[
        {name: '全款车押车', id: '全款车押车'},
        {name: '全款车押证', id: '全款车押证'},
        {name: '公司车押证', id: '公司车押证'},
        {name: '河源惠州牌押证', id: '河源惠州牌押证'},
        {name: '信用贷', id: '信用贷'}
      ],
      types:[
        {name: '公司', id: '公司'},
        {name: '个人', id: '个人'}
      ],
      companies:[
        {name: '龙华', id: '龙华'},
        {name: '宝安', id: '宝安'},
        {name: '东莞', id: '东莞'},
        {name: '惠州', id: '惠州'}
      ],
      contract:{
        product: null,
        type: '个人',
        company: null
      }
    }
  },
  computed:{
    info(){
      return this.item.wind_control;
    }
  },
  methods:{
    onEditContractNumber(){
      let uid = sessionStorage.getItem('ts_user_id');
      if(this.editContactNumber){
        return rest({
        url: '/v1/inspect/'+ uid +'/bill/contract_number/update',
        headers:{
          'X-Inspect-Token': sessionStorage.getItem('ts_userToken')
        },
        method: 'POST',
        data: {
          bill_id: this.target.bill_info.bill_id,
          contract_number: this.target.bill_info.contract_number
        }
        }).then(res => {
          this.editContactNumber = !this.editContactNumber;
          this.$message({
            message: '编辑合同号成功！',
            type: 'success'
          });
        }).catch(res => {
          this.$message.error(res.response.data.msg);
        })
      }else{
        this.editContactNumber = !this.editContactNumber;
      }
    },
    handlePreview(file){
      window.open(file.response);
    },
    beforeRemove(file, fileList) {
      return this.$confirm(`确定移除 ${ file.name }？`);
    },
    handleLoanContractChange(file, fileList){
      this.item.loan_contract = fileList;
    },
    handleMortgageContractChange(file, fileList){
      this.item.mortgage_contract = fileList;
    },
    handleCarTransferChange(file, fileList){
      this.item.car_transfer = fileList;
    },
    handleLoanIntermediaryChange(file, fileList){
      this.item.loan_intermediary = fileList;
    },
    handleRepaymentPlanChange(file, fileList){
      this.item.repayment_plan = fileList;
    },
    handleLoanReceiptChange(file, fileList){
      this.item.loan_receipt = fileList;
    },
    handleAttorneyPowerChange(file, fileList){
      this.item.attorney_power = fileList;
    },
    handleFinanceServicesAgreementChange(file, fileList){
      this.item.finance_services_agreement = fileList;
    },
    handleLoanApprovalFormChange(file, fileList){
      this.item.loan_approval_form = fileList;
    },
    handleOtherChange(file, fileList){
      this.item.other = fileList;
    },
    onCheckEvent(){
      this.$emit('detail');
    },
    closeEvent(){
      const that = this;
      this.visible = false;
      setTimeout(()=>{that.$emit('close');},500);
    },
    onDownloadContract(){
      let uid = sessionStorage.getItem('ts_user_id');
      
      if(this.contract.company === null){
        this.$message('请选择公司！');
        return false;
      }
      if(this.contract.type === null){
        this.$message('请选择类型！');
        return false;
      }
      if(this.contract.type === '个人' && this.contract.product === null){
        this.$message('请选择产品！');
        return false;
      }
      window.open(`/v1/inspect/${ uid }/contract?bill_id=${this.item.bill_id}&company=${this.contract.company}&type=${this.contract.type}${this.contract.type === '个人'?'&product='+this.contract.product:''}`);
    },
    onPassEvent(){
      let data = ObjCopy(this.item);
      if(data.loan_contract.length === 0){
        this.$message('请上传借款合同！');
        return false;
      }else{
        delete data.loan_contract;
        data.loan_contract = this.item.loan_contract.map(item => item.response?item.response:item.url);
      }
      if(data.mortgage_contract.length === 0){
        this.$message('请上传抵押合同！');
        return false;
      }else{
        delete data.mortgage_contract;
        data.mortgage_contract = this.item.mortgage_contract.map(item => item.response?item.response:item.url);
      }
      if(data.car_transfer.length === 0){
        this.$message('请上传车辆转让协议！');
        return false;
      }else{
        delete data.car_transfer;
        data.car_transfer = this.item.car_transfer.map(item => item.response?item.response:item.url);
      }
      if(data.loan_intermediary.length === 0){
        this.$message('请上传借款居间协议！');
        return false;
      }else{
        delete data.loan_intermediary;
        data.loan_intermediary = this.item.loan_intermediary.map(item => item.response?item.response:item.url);
      }
      if(data.repayment_plan.length === 0){
        this.$message('请上传还款计划表！');
        return false;
      }else{
        delete data.repayment_plan;
        data.repayment_plan = this.item.repayment_plan.map(item => item.response?item.response:item.url);
      }
      if(data.loan_receipt.length === 0){
        this.$message('请上传借款收据！');
        return false;
      }else{
        delete data.loan_receipt;
        data.loan_receipt = this.item.loan_receipt.map(item => item.response?item.response:item.url);
      }
      if(data.attorney_power.length === 0){
        this.$message('请上传机动车委托书！');
        return false;
      }else{
        delete data.attorney_power;
        data.attorney_power = this.item.attorney_power.map(item => item.response?item.response:item.url);
      }
      if(data.finance_services_agreement.length === 0){
        this.$message('请上传融资服务协议！');
        return false;
      }else{
        delete data.finance_services_agreement;
        data.finance_services_agreement = this.item.finance_services_agreement.map(item => item.response?item.response:item.url);
      }
      if(data.loan_approval_form.length === 0){
        this.$message('请上传放款审批表！');
        return false;
      }else{
        delete data.loan_approval_form;
        data.loan_approval_form = this.item.loan_approval_form.map(item => item.response?item.response:item.url);
      }
      if(data.other.length === 0){
        
      }else{
        delete data.other;
        data.other = this.item.other.map(item => item.response?item.response:item.url);
      }
      this.$emit('pass', data);
    }
  },
  mounted(){
    if(!this.target.sign.sign_id){
      this.item = {   
        "bill_id": this.target.bill_info.bill_id,                //订单id 
        "bill_status": 4,           //审核之后的订单状态 (驳回到之前的任意一个审核状态，或者下个状态)
        "status": 1,                 //1通过 -1驳回
        "opinion": "无",         //审核意见
        // "sign_id": 1,      //更新的时候才需要带上

        "flag": 1,          //0表示公司 1表示个人
        "loan_contract": [], //借款合同
        "mortgage_contract": [],   //抵押合同
        "car_transfer":[],            //车辆转让协议
        "loan_intermediary":[],         //借款居间协议
        "repayment_plan":[],            //还款计划表
        "loan_receipt":[],              //借款收据
        "attorney_power":[],            //机动车委托书
        "finance_services_agreement":[],//融资服务协议
        "loan_approval_form":[],        //放款审批表
        "other":[],                     //其他资料
      }
    }else{
      this.item = this.target.sign;
      this.item.loan_contract = this.item.loan_contract.map(item => {
        return {name: '查看', url: item};
      });
      this.item.mortgage_contract = this.item.mortgage_contract.map(item => {
        return {name: '查看', url: item};
      });
      this.item.car_transfer = this.item.car_transfer.map(item => {
        return {name: '查看', url: item};
      });
      this.item.loan_intermediary = this.item.loan_intermediary.map(item => {
        return {name: '查看', url: item};
      });
      this.item.repayment_plan = this.item.repayment_plan.map(item => {
        return {name: '查看', url: item};
      });
      this.item.loan_receipt = this.item.loan_receipt.map(item => {
        return {name: '查看', url: item};
      });
      this.item.attorney_power = this.item.attorney_power.map(item => {
        return {name: '查看', url: item};
      });
      this.item.finance_services_agreement = this.item.finance_services_agreement.map(item => {
        return {name: '查看', url: item};
      });
      this.item.loan_approval_form = this.item.loan_approval_form.map(item => {
        return {name: '查看', url: item};
      });
      this.item.other = this.item.other.map(item => {
        return {name: '查看', url: item};
      });
      this.item.bill_id = this.target.bill_info.bill_id;
      this.item.bill_status = 4;
      this.item.flag = 1;
    }
  }
}
</script>
<style scoped>
  .m-orderManage-auditDialog .detail-title {
    line-height: 24px;
    font-size: 16px;
    color: #2d2f33;
    border-bottom: 1px #ddd solid;
    margin-bottom: 10px;
    padding: 0 30px;
  }
  .m-orderManage-auditDialog .el-form {
    padding: 20px;
  }
  .m-orderManage-auditDialog .score_card_panel{
    border: 2px #ddd dotted;
    margin-bottom: 20px;
  }
  .m-orderManage-auditDialog .score_card_panel .title{
    padding: 20px;
    font-size: 16px;
    font-weight: bold;
  }
</style>




